div.tx-item {
    border-bottom: 1px solid #f0f0f0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 5px;
}

div.tx-item:nth-child(even){
    background-color: #eee;
}

div.tx-item div {
    flex: 1;
    text-align: center;
}

