ul.heading{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin:0;
    padding: 0;
    /* border: 1px solid #999; */
    width: 100%;
}

.heading li{
    list-style: none;
    /* border: 1px solid #999; */
    text-align:center;
}

.highlight {
    font-weight: bold;
}

li.note {
    flex: 5;
}

li.newstart, li.lastname, li.status, li.tx_site, li.tx_modality, li.tx_machine, li.note {
    flex: 3;
}

li.status {
    font-weight: bold;
}

li.planners, li.doctors{
    flex: 1;
}

@media only screen and (max-width:375px){
    li.planner{
        display: none;
    }

    li.doctor{
        display: none;
    }

    li.tx_site{
        display: none;
    }
}

.plan-form{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}

div.text-field-note {
    margin-top: 10px;
}

div.text-field-note input {
    padding: 0.7em;
}

.text-field-tx-site input:disabled {
    color: black;
}

@media only screen and (max-width: 375px){
    .plan-form{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-around;
        width: 100%;
    }

    .plan-form > div{
        width: 100%;
    }
}


