.user-settings-content {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
}

div.card{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border:1px solid #ddd;
    border-radius: 3px;;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
    padding: 1em;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.1);
    width: 768px;
}

div.card-title{
    font-weight: 600;
    padding: 5px;
    text-decoration: underline;;
}

div.card-content{
    padding: 5px;
}

.user-settings-item-wrapper{
    padding: 5px 0px;
}

.item-label {

}

.item-input {
    float: right;
    text-align: center;
}

.item-input > span {
    margin-right: 5px;
}


.item-helper-text {
    font-size: 0.8em;
    color: gray;
    margin-top: 3px;
}

@media only screen and (max-width: 375px){
    div.card{
        width: 100%;
        margin-bottom: 5px;
        font-size: 0.9em;
    }
}

