ul.log-heading{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    /* border: 1px solid red; */
    width: 100%;
    margin: 0px;
    list-style: none;
}

li {
    flex: 1;
    flex-basis: 0;
}

li.log-status{
    font-weight: bold;
}




