div.gamma-title {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: #0e4087;
    padding: 10px;
    color: white;
    font-weight: bold;
    margin-bottom: 1em;
    font-size: 1em;
}

div.gamma-wrapper {
    font-size: 1em;
}

div.gamma-wrapper > select {
    border: none;
    border-bottom: 1px solid black;
    font-size: 1em;
}

div.gamma-wrapper > input {
    border: none;
    border-bottom: 1px solid black;
    width: 50px;
    text-align: center;
    font-size: 1em;
}

