div.item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
    margin-bottom: 10px;
}

ul.schedule_item {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    /* align-items: center; */
    flex: 9
}

ul.schedule_item > li {
    list-style: none;
}

button.add_button{
    flex: 1;
    margin-left: 20px;
}


@media only screen and (max-width:375px){
    div.item {
        display: block;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-right: 30px;
    }

    li.PatientId {
        display: none;
    }

    button.add_button{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top : 5px;
        margin-bottom: 5px;
    }

}