div.qa_item{
    display: flex;
    flex-wrap: wrap;
    /* border: 1px solid red;  */
    width: 100%;
    list-style: none;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 1em;
    text-align: center;
}

div.qa_item > div{
    min-width: 100px;;
    margin: 5px 0px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 3px;    
}

div.qa_item input{
    padding: 5px;
    width: 80%;
}

div.qa_item button:enabled{
    font-weight: bold;
}

.button-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.button-container button{
    padding: 5px 10px;
}

div.qa-result {
    display: inline;
}

button.open-gamma-dlg{
    display: inline;
    width: 20px;
    margin: 0 5px;
    padding: 0;
    float: right;
}


@media only screen and (max-width:375px){
    div.qa_item{
        flex-direction: column;
        border: 1px solid #cccc;
        margin: 5px;
    }

    div.qa_item > div{
        width: 100%;
    }

}