.search-wrapper {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.search-input-wrapper input{
    padding: 5px;
    width: 80%;
    font-size: 1em;
}

.matched-list > div {
    padding-left: 5px;
    cursor: pointer;
    font-size: 0.95em;
}

.matched-list > div:hover {
    padding-left: 5px;
    cursor: pointer;
    background-color: lightgray;
}